import React, { lazy, useEffect, useState } from 'react'
import Select from 'react-select';
import {
  CCol,
  CSelect,
  CCard,
  CCardBody,
  CLink,
  CCollapse,
  CModal,
  CFormGroup,
  CInputCheckbox,
  CButton,
  CLabel,
  CInput,
  CProgress,
  CRow,
  CCallout,
  CDataTable,
  CTextarea,
  CModalHeader,
  CModalBody,
  CModalFooter,
  CForm
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import {subkomoditasgaram, subsubkomoditasgaram, stdmutugaram, hscodegaram, subkomoditasberas,subsubkomoditasberas,stdmutuberas,hscodeberas, subkomoditasikan, subsubkomoditasikan, stdmutuikan, hscodeikan} from './sumber.js'
import { useSelector } from 'react-redux';
import store from '../../redux/store'

const FormImpor = (props) => {
    const [check, setCheck] = useState(false)
    const handleCheck = () =>{
        setCheck(!check)
    }
    const jenisPerijinan = useSelector(state=>state.jenisPerijinan)
  return (
      <>
      <CCard>
          <CCardBody>
              <div className="d-flex flex-row justify-content-start pb-3">
                <div className="pr-3">
                    <i class="fas fa-file"></i>
                </div>
                <div>
                    <h5>Checkpoint Kelengkapan Data</h5>
                </div>
              </div>
              <div className="d-flex flex-row justify-content-center">
                  <div className="col">
                      <CCard>
                          <CCardBody>
                              <div className="d-flex flex-row">
                                <div style={{color:"green"}} className="pr-5">
                                    <i class="fas fa-check fa-3x"></i>
                                </div>
                                <div>
                                    <b><h6>Profil Pelaku Usaha</h6></b>
                                    <small>Data Lengkap</small>
                                </div>                                  
                              </div>
                          </CCardBody>
                      </CCard>
                  </div>                
                  {
                      jenisPerijinan == "1" || jenisPerijinan == "2" || jenisPerijinan == "3" ?
                      <>
                        <div className="col">
                            <CCard>
                                <CCardBody>
                                    <div className="d-flex flex-row">
                                        <div style={{color:"green"}} className="pr-5">
                                            <i class="fas fa-check fa-3x"></i>
                                        </div>
                                        <div>
                                            <h6>Data Komoditas</h6>
                                            <small>Data Lengkap</small>
                                        </div>                                  
                                    </div>
                                </CCardBody>
                            </CCard>
                        </div>                       
                      </>
                      :
                      <>
                      </>
                  }
                  <div className="col">
                      <CCard>
                          <CCardBody>
                              <div className="d-flex flex-row">
                                <div style={{color:"green"}} className="pr-5">
                                    <i class="fas fa-check fa-3x"></i>
                                </div>
                                <div>
                                    <h6>Data Persyaratan</h6>
                                    <small>Data Lengkap</small>
                                </div>                                  
                              </div>
                          </CCardBody>
                      </CCard>
                  </div>                                  
              </div>
              <div className="d-flex">
                  <CCard style={{width:"100%"}}>
                      <CCardBody>
                          <div className="d-flex flex-column justify-content-start">
                          <div>
                                  <h5><b>Disclaimer</b></h5>
                              </div>
                              <div>
                                  <h6>1. Dengan ini saya menyatakan bertanggung jawab atas kebenaran hal-hal yang diberitahukan dalam dokumen ini</h6>
                              </div>
                              {/* <div>
                                  <h6>2. Saya Menyatakan Bahwa : </h6>
                              </div> 
                              <div className="pl-2">
                                  <h6>a. Rencana impor dalam permohonan ini diajukan sesuai kebutuhan riil industri; dan </h6>
                              </div> 
                              <div className="pl-2">
                                  <h6>b. komoditas garam yang diberitahukan dalam permohonan ini dimaksudkan untuk penggunaan sendiri sebagai bahan baku industri dan tidak diperdagangkan dan/atau dipindahtangankan kepada pihak lain </h6>
                              </div>                                                                                          
                              <div style={{width:"100%"}}>

                              </div> */}
                          </div>
                      </CCardBody>
                  </CCard>
              </div>
              <div className="d-flex justify-content-between">
                <div className="d-flex flex-row">
                        <div className="pr-2">
                            <input value={check} onClick={()=>handleCheck()} type="checkbox"/>                          
                        </div>
                        <div>
                            <CLabel>Saya Menyetujui</CLabel>                          
                        </div>                          
                  </div>
                <div>
                <i class="fas fa-file"></i> Cetak Laporan
                </div>                  
              </div>
          </CCardBody>
      </CCard>
      <div className="d-flex flex-row justify-content-between">
          <div>
              <CButton className="btn btn-secondary"><i class="far fa-save"></i> Simpan Sebagai Draft</CButton>
          </div>
          <div>
              <CLink disabled={!check} role="button" to="/ssm-izin" className="btn btn-success"><i class="fas fa-paper-plane"></i> Kirim Pengajuan</CLink>
          </div>
      </div>
      </>
  )
}

export default FormImpor
